<template>
	<div style="background: #f7f7f7;">
		<headerNav title="订单详情"/>

		<van-cell-group>
			<van-cell title="订单编号" :value="orderDetails.OrderNO"/>
			<van-cell title="取货方式" :value="orderDetails.expressType"/>
			<van-cell title="取货时间" :value="orderDetails.pickUpTime"/>
		</van-cell-group>
		<div style="height:5px;"></div>

		<van-cell-group>
			<van-field v-model="orderDetails.address" label="地址" type="textarea" autosize readonly/>
		</van-cell-group>
		<div style="height:5px;"></div>

		<van-cell-group>
			<van-field v-model="orderDetails.remark" label="备注" type="textarea" autosize readonly/>
		</van-cell-group>
		<div style="height:5px;"></div>

		<div class="card" v-for="(product, index) in orderDetails.content" :key="index">
			<product-card :product='product' :isOrder="true"/>
		</div>
		<div style="height:5px;"></div>

		<van-cell-group>
			<van-cell title="商品总数" :value="totalCount"/>
			<van-cell title="合计金额" :value="`￥${totalPrice}`" value-class="totalPrice"/>
		</van-cell-group>
	</div>
</template>

<script>
	import productCard from '../../../components/product/productcard'
	import headerNav from '../../../components/header/nav'
	import {mapState, mapActions} from 'vuex';

	export default {
		name: 'OrderDetail',

		components: {
			productCard,
			headerNav
		},

		data() {
			return {
				totalPrice: 0,
			}
		},

		computed: {
			...mapState(['orderResult', 'orderDetails']),

			totalCount: {
				get() {
					let total = 0;
					this.orderDetails.content.forEach(item => {
						total += item.GoodsCount;
					});
					return total;
				}
			},
		},

		async mounted() {
			let row = this.orderResult.content[this.$route.query.index];

			let address = `${row.RcName} ${row.RcPhone} ${row.RcAddr}`;
			let expressType;
			if (row.OrderType === '订货单') {
				expressType = row.ExpressType === 1 ? '快递物流' : '自提';
			} else {
				expressType = null;
			}
			let pickUpTime = row.PickupTime || '';
			if (pickUpTime) {
				pickUpTime = pickUpTime.replace('T', ' ');
			}
			await this.loadOrderDetails([row.Out_Trade_No, row.remark, address, expressType, pickUpTime]);
			this.initTotalPrice();
		},

		methods: {
			...mapActions(['loadOrderDetails']),

			initTotalPrice() {
				this.totalPrice = 0;
				this.orderDetails.content.forEach(item => {
					this.totalPrice += item.totalPrice;
				});
			}
		},
	}
</script>

<style lang="less">
	.logistics {
		margin-top: 15px;
		i {
			line-height: 60px;
			font-size: 20px;
		}
		.van-cell__title span {
			overflow: hidden;
			text-overflow: ellipsis;
			display: -webkit-box;
			-webkit-line-clamp: 2;
			-webkit-box-orient: vertical;
		}
		.van-cell__label {
			color: #999;
		}
	}

	.total {
		.van-cell__value {
			color: red;
		}
	}

	.footer {
		height: 50px;
		.munu {
			position: fixed;
			height: 49px;
			border-top: 1px solid #e5e5e5;
			bottom: 0;
			background: #fff;
			width: 100%;
			line-height: 24px;
			text-align: right;
			.van-button {
				margin-right: 10px;
			}
		}
	}

	.totalPrice {
		color: red;
		font-size: 18px;
		font-weight: bold;
	}
</style>



